import { SidebarTopIcon, ToggleSidebarIcon } from "@/assets/icons";
import { sidebarLogo } from "@/assets/images";
import Image from "next/image";

export const Sidebar = () => {
  return (
    <aside className="fixed bottom-0 right-0 w-12  overflow-hidden z-[999] border-r-2 border-cerulean">
      {/* top logo */}
      <div
        className={`bg-white h-12  shadow-sidebarWadow flex items-center justify-center `}
      >
        <Image src={sidebarLogo} alt="logo" width={42} />
      </div>
      <div>
        
      </div>
      {/* bottom */}
      <div className="bg-white py-3 flex flex-col gap-5 items-center shadow-sidebarWadow">
        <button>
          <ToggleSidebarIcon />
        </button>
        <button className="w-[34px] h-[34px] bg-superSilver rounded-full flex items-center justify-center">
          <SidebarTopIcon />
        </button>
      </div>
    </aside>
  );
};

export default Sidebar;
